.block.is_contacts {
  position: relative;
  min-height: 720px;
  z-index: 10;
}
.block-content.is_contacts {
  z-index: 15;
  /* pointer-events: none; */
}
.contacts {
  background-color: rgba(255, 255, 255, 0.9);
  border-radius: 3px;
  padding: 50px;
  box-shadow: 0px 3px 7px 0px rgba(0, 0, 1, 0.14);
  min-height: 520px;
}
.contacts-map {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 11;
}
