.footer {
  background: $themeFooterBackground;
  color: $themeLightText;
  padding: 30px 0;
  min-height: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  &-content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex: 0 1 $contentWidth;
    padding: 0 $gap;
  }
}
