.topBlock {
  width: 100%;
  height: 500px;
  min-height: 500px;
  height: 100vh;
  overflow: hidden;
  position: relative;
  background-color: #001543;
  background-image: radial-gradient(
    150% 100% at center top,
    #11adf7 0%,
    #001543 42%
  );
  color: $themeTopBlockTextColor;
  font-size: 1em;
  font-size: 2vmin;
  &-wrap {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  &-content {
    flex: 0 1 $contentWidth;
    padding: 0 $gap;
    animation-delay: 0.1s;
    animation: fadeIn 1s;
    z-index: 100;
  }
  &-header {
    line-height: 1.3;
    &-subtitle {
      font-size: 1.12em;
      text-transform: uppercase;
      letter-spacing: 0.05em;
    }
    &-title {
      font-size: 3em;
      line-height: 1.3;
      font-weight: 700;
      text-transform: uppercase;
      margin: 0.25em 0;
      letter-spacing: 0.04em;
    }
    &-slogan {
      font-size: 1.12em;
      letter-spacing: 0.05em;
    }
  }
  &-body {
    margin-top: 6em;
  }

  &-background {
    position: absolute;
    overflow: hidden;
    top: -5%;
    left: 0;
    bottom: 0;
    right: 0;
    width: 100%;
    z-index: 1;
    opacity: 0.8;
    background: $themeDarkBackground url(/assets/img/bg.jpg) no-repeat;
    /* background-color: $themeDarkBackground; */
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 50%;
  }
  &-scroll {
    position: absolute;
    bottom: 150px;
    left: 50%;
    transform: translate(-50%);
    z-index: 1000;
  }
}
.scrollDown {
  &-sign {
    fill: $themeTopBlockTextColor;
    top: 0;
  }
  &-arrow {
    fill: $themeTopBlockTextColor;
    position: absolute;
    left: 50%;
    transform: translate(-50%, 0);
    top: 50%;
    transition: 0.4s;
  }
  &:hover .scrollDown-arrow {
    top: 60%;
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 100%;
  }
}

@media only screen and (width <= 640px) {
  .topBlock {
    font-size: 0.6em;
    font-size: 3vmin;
    height: auto;
    &-background {
      background-size: cover;
      background-position: 60%;
    }
    &-wrap {
      position: relative;
      margin-top: 150px;
      margin-bottom: 115px;
      align-items: flex-start;
    }
    &-scroll {
      display: none;
    }
  }
}
