.carousel-item {
  margin-right: 15px;
}

.carousel-item-img {
  opacity: 0;
  transition: opacity 0.3s;
  display: block;
  max-height: 100%;
  max-width: 100%;
  width: 100%;
  height: auto;
}
/* fade in lazy loaded image */
.carousel-item-img.flickity-lazyloaded,
.carousel-item-img.flickity-lazyerror {
  opacity: 1;
}

.flickity-prev-next-button {
  background-color: transparent;
  opacity: 0.34;
  transition: 0.3s;
}

.flickity-prev-next-button .arrow {
  fill: #4c4c4c;
}
.flickity-prev-next-button.no-svg {
  color: #4c4c4c;
}
/* hide disabled button */
.flickity-prev-next-button:disabled {
  fill: #4c4c4c;
}
