@import "./logo";
@import "./menu";
@import "./phone";
@import "./hamburger";

.header {
  position: absolute;
  font-family: "m";
  top: 30px;
  left: 0;
  right: 0;
  width: 100%;
  z-index: 9999;
  display: flex;
  justify-content: center;
  &-content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex: 0 1 $contentWidth;
    padding: 0 $gap;
  }
  &-logo {
    flex: 0 1 160px;
    min-width: 100px;
  }
  &-menu {
  }
  &-contact {
    display: flex;
    &-email {
      margin-left: 30px;
    }
    &-phone {
      display: flex;
      align-items: center;
    }
  }
  &-hamburger {
    display: none;
  }
}

@media only screen and (width <= 1099px) {
  .header {
    &-menu {
      display: none;
    }
    &-contact {
      width: 100%;
      justify-content: flex-end;
    }
    &-hamburger {
      display: flex;
      align-items: center;
      flex: 0 1 30px;
      min-width: 20px;
    }
  }
}
@media only screen and (width <= 640px) {
  .header {
    top: 5px;
    &-content {
      flex-flow: wrap;
    }
    &-logo {
      flex: 0 1 80px;
      min-width: 60px;
    }
    &-hamburger {
      flex: 0 1 20px;
      min-width: 16px;
    }
    &-contact {
      flex-shrink: 1;
      justify-content: center;
      order: 3;
      border-bottom: 1px solid rgba(255, 255, 255, 0.34);
      padding-bottom: 15px;
      &-email {
        display: none;
      }
    }
  }
}
