.logo {
  margin-right: 3rem;
  display: flex;
  align-items: center;
  &:any-link {
    color: transparent;
  }
  &-name {
    position: absolute;
    text-indent: -1000em;
    font-weight: 400;
    color: color(#fff alpha(-100%));
    margin: 0;
    padding: 0;
  }
  &-sign {
    width: 100%;
    fill: $themeHeaderTextColor;
  }
}
