.menu {
  &-items {
    display: flex;
    justify-content: flex-start;
  }
  &-item {
    margin-right: 18px;
    &-link {
      white-space: nowrap;
      display: inline-block;
      position: relative;
      &:any-link {
        color: $themeHeaderTextColor;
      }
      &:hover {
        /* border-bottom: 1px solid $themeLinkActive; */
      }
      &:before {
        content: "";
        position: absolute;
        width: 100%;
        height: 1px;
        bottom: -8px;
        left: 0;
        background-color: $themeLinkActive;
        visibility: hidden;
        transform: scaleX(0);
        transition: all 0.1s ease-in-out 0s;
      }
      &:hover:before {
        visibility: visible;
        transform: scaleX(1);
      }
    }
  }
}
