.button {
  text-align: center;
  display: inline-block;
  border-radius: 500px;
  border: 1px solid $themeHColor;
  white-space: nowrap;
  padding: 0 1em;
  line-height: 2.6em;
  user-select: none;
  &:any-link {
    color: $themeHColor;
    transition: 0.4s;
  }
  &:hover {
    background-color: $themeLinkActive;
    border: 1px solid $themeLinkActive;
  }
  &.is_header {
    font-size: 1.4rem;
    border-color: $themeHeaderTextColor;
    &:any-link {
      color: $themeHeaderTextColor;
    }
    &:hover {
      background-color: $themeLinkActive;
      border: 1px solid $themeLinkActive;
    }
  }
  &.is_topSection {
    font-size: 1.3em;
    border-color: $themeTopSectionTextColor;
    &:any-link {
      color: $themeTopSectionTextColor;
    }
    &:hover {
      background-color: $themeLinkActive;
      border: 1px solid $themeLinkActive;
    }
  }
}
