.block.is_projects {
  background-color: $themeLightBackground;
}
.block-row.is_projects_description {
  min-height: 300px;
}
.projects-cosmonaft {
  position: absolute;
  top: -40px;
  right: 5%;
  width: 300px;
  height: 460px;
  font-size: 193px;
  &-img {
    position: absolute;
    left: 50%;
    transform: translate(-50%);
  }
  &-shadow {
    position: absolute;
    left: 50%;
    transform: translate(-50%);
    bottom: 0;
  }
}

.carousel-items.is_projects {
  display: flex;
  flex-wrap: wrap;
  width: 100%;

  &:after {
    display: none;
    content: "";
  }
}

.carousel-item-wrap.is_projects {
  flex: 0 1 360px;
  margin: $gap;
}
.carousel-item.is_projects {
  width: 100%;
  display: block;
}
.carousel-item-img.is_projects {
  width: 100%;
  height: auto;
  display: inline-block;
  transition: 0.4s;
  border-radius: 3px;
}
.project-title {
  color: $themeHColor;
  font-size: 1.5rem;
  font-weight: 500;
  text-transform: uppercase;
  margin-top: 10px;
}
.project-description {
  color: $themeGrayText;
  font-size: 1.5rem;
  font-weight: 400;
}

.carousel-item.is_projects:hover .carousel-item-img.is_projects {
  box-shadow: 2px 13px 22.56px 1.44px rgba(0, 0, 1, 0.22);
}

@media only screen and (width <= 1023px) {
  .carousel-items.is_projects {
    &:after {
      content: "flickity";
    }
    display: block;
    width: 100%;
    height: auto;
    padding: 0 80px;
  }
  .carousel-item-wrap.is_projects {
    margin-right: 0 10px 0 0;
    width: 380px;
    height: 330px;
    padding: 5px 10px 0 10px;
  }
  .projects-cosmonaft {
    display: none;
  }
}

@media only screen and (width <= 640px) {
  .block-row.is_projects {
    margin-left: -15px;
    margin-right: -15px;
  }
  .block-title.is_p {
    margin-left: $gap;
    margin-right: $gap;
    text-align: left;
    font-size: 4.2rem;
    &:after {
      display: block;
    }
  }
  .carousel-item-wrap.is_projects {
    margin: 0 10px 0 0;
    width: 70%;
    height: auto;
    padding: 5px 10px 0 10px;
  }
  .carousel-items.is_projects {
    padding: 0;
    .flickity-prev-next-button {
      display: none;
    }
  }
}
