.deck-item.is_reviews {
  user-select: none;
  background-color: #fff;
  cursor: hand;
  transform-style: preserve-3d;
  border-radius: 5px;
  box-shadow: 2px 14px 35px 0px rgba(0, 0, 0, 0.17);
  padding: 60px 120px;
  position: absolute;
  /* top: -20px;
  left: -20px; */
  width: 100%;
  height: 100%;
  z-index: 15;
  & * {
    user-select: none;
  }
  &:after {
    content: "“";
    font-family: arial, sans-serif;
    position: absolute;
    top: 30px;
    left: 50px;
    color: $themeHColor;
    opacity: 0.34;
    font-size: 12rem;
    line-height: 1;
  }
}

.deck-title {
  color: $themeHColor;
  font-size: 3rem;
  font-weight: 500;
  margin: 5px 0 20px 0;
}
.deck-subtitle {
  color: $themeGrayText;
}
.deck-text {
  font-size: 1.8rem;
}

.deck-footer {
  position: absolute;
  bottom: 30px;
  right: 50px;
}

.deck-link {
  &:any-link {
    fill: $themeLinkColor;
  }
  &:hover {
    fill: $themeLinkActive;
  }
}

@keyframes animation {
  from {
    opacity: 0;
    transform: scale(1.2) rotateX(45deg);
  }
