.block.is_news {
  background-color: $themeLightBackground;
}

.news-item {
  display: block;
  background-color: #fff;
  border-radius: 3px;
  padding: 50px;
  min-height: 320px;
  margin-bottom: calc($gap*2);
  transition: 0.4s;
  display: flex;
  flex-direction: column;
  &:hover {
    box-shadow: 1px 10px 25px 0px rgba(0, 0, 1, 0.17);
  }
}
.news-date {
  color: $themeGrayText;
  font-weight: 400;
  line-height: 1;
}
.news-details {
  align-self: flex-end;
  border-bottom: 1px solid transparent;
}

.news-item:hover .news-details {
  border-bottom: 1px solid $themeLinkActive;
}

.news-title {
  color: $themeHColor;
  font-size: 2.4rem;
  font-weight: 500;
  margin: 15px 0 10px 0;
}
.news-description {
  font-weight: 400;
  color: $themeTextColor;
  height: 120px;
}

/* @media only screen and (width <= 640px) {
  .news-item {
    padding: $gap;
  }
} */

@media only screen and (width <= 1023px) {
  .block.is_news {
    display: none;
  }
}
