.icons {
  &-items {
    display: flex;
    flex-wrap: wrap;
  }
  &-item {
    margin: 0 $gap $gap 0;
    flex: 0 1 60px;
    display: flex;
    justify-content: center;
    align-items: center;
    &-img {
      width: 100%;
      height: auto;
      display: inline-block;
    }
  }
}

.directions {
  margin-top: 80px;
}
.direction {
  padding-bottom: $gap;
  padding-right: 0;
  min-height: 267px;
  position: relative;
  &-link {
    display: flex;
    flex-direction: column-reverse;
    justify-content: space-between;
    width: 100%;
    height: 100%;
    background-color: $themeDarkBackground;
    padding: 32px;
    border-radius: 3px;
    background-size: cover;
    transition: 0.4s;
    &:any-link {
      color: $themeLightText;
    }
    &:hover {
      box-shadow: 1px 10px 29px 0px rgba(0, 0, 0, 0.64);
    }
  }
  &-title {
    font-size: 3.3rem;
    font-weight: 700;
  }
  &-icon {
    width: 90px;
    height: 90px;
    border: 1px solid $themeLightText;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 100%;
    &-img {
      fill: $themeLightText;
    }
  }
}

@media only screen and (width <= 1023px) {
  .icons {
    margin-top: 30px;
  }
  .direction {
    flex: 0 1 100%;
    width: 100%;
    &-link {
    }
  }
}

@media only screen and (width <= 640px) {
  .directions {
    margin-top: 30px;
  }
  .direction {
    min-height: auto;
    &-title {
      font-size: 2rem;
      font-weight: 400;
    }
    &-link {
      background-color: transparent;
      background-image: none !important;
      padding: 0;
      margin-bottom: 20px;
      &:any-link {
        color: $themeLinkColor;
        text-decoration: underline;
      }
      &:hover {
        color: $themeLinkActive;
        box-shadow: none;
      }
    }
  }
}
