.block {
  display: flex;
  justify-content: center;
  width: 100%;
  &-content {
    flex: 0 1 $contentWidth;
    padding: $topPadding $gap $bottomPadding $gap;
    position: relative;
  }
  &-title {
    text-transform: uppercase;
    font-size: 2.75em;
    font-weight: 500;
    position: relative;
    color: $themeHColor;
    margin-bottom: 30px;
    max-width: calc(100vw-2*$gap);
    word-wrap: break-word;
    &:after {
      position: absolute;
      content: "";
      height: 2px;
      width: 120px;
      background-color: $themeHeadStroke;
      opacity: 0.22;
      left: 0;
      bottom: -10px;
    }
  }
  &-row {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -$gap;
  }
  &-half {
    flex: 0 1 50%;
    padding: 0 $gap;
  }
  &-2x3 {
    flex: 0 1 66%;
    padding: 0 $gap;
  }
  &-1x3 {
    flex: 0 1 33%;
    padding: 0 $gap;
  }
  &-full {
    flex: 0 1 100%;
    padding: 0 $gap;
  }
  &-actions {
    margin-top: 56px;
    display: inline-block;
  }
  &-link {
    font-size: 1.8rem;
    display: flex;
    align-items: center;
    &-wrap {
      margin-left: 16px;
    }
    &:any-link {
      .block-link-title {
        border-bottom: 1px solid $themeLinkColor;
      }
      .block-link-icon {
        fill: $themeLinkColor;
      }
    }
    &:hover {
      .block-link-title {
        border-bottom: 1px solid $themeLinkActive;
      }
      .block-link-icon {
        fill: $themeLinkActive;
      }
    }
  }
}

@media only screen and (width <= 1023px) {
  .block {
    &-row {
      margin: 0;
    }
    &-half {
      width: 100%;
      flex: 0 1 100%;
      padding: 0;
    }
    &-2x3 {
      width: 100%;
      flex: 0 1 100%;
      padding: 0;
    }
    &-1x3 {
      width: 100%;
      flex: 0 1 100%;
      padding: 0;
    }
    &-full {
      width: 100%;
      flex: 0 1 100%;
      padding: 0;
    }
  }
}

@media only screen and (width <= 640px) {
  .block {
    &-title {
      font-size: 3.8rem;
    }
  }
}
