.block.is_clients {
  min-height: 400px;
  background-color: $themeLightBackground;
}

.block-content.is_clients {
  padding-top: 0;
}

.block-title.is_clients {
  text-align: center;
  font-size: 3.6rem;
  &:after {
    display: none;
  }
}

.carousel-items.is_clients {
  width: 100%;
  height: 160px;
  padding: 0 80px;
}
.carousel-item-wrap.is_clients {
  margin-right: 10px;
  width: 235px;
  height: 170px;
  padding: 5px 10px 0 10px;
}
.carousel-item.is_clients {
  width: 100%;
  height: 140px;
  padding: 15px;
  background-color: #fff;
  border-radius: 3px;
  box-shadow: 2px 13px 15.84px 0.16px rgba(0, 0, 0, 0.08);
}

@media only screen and (width <= 640px) {
  .block-content.is_clients {
    padding-left: 0;
    padding-right: 0;
  }
  .block-title.is_clients {
    margin-left: $gap;
    margin-right: $gap;
    text-align: left;
    font-size: 3.8rem;
    &:after {
      display: block;
    }
  }
  .carousel-items.is_clients {
    padding: 0;
    .flickity-prev-next-button {
      display: none;
    }
  }
}
