.phone {
  font-size: 2.2rem;
  font-weight: 400;
  &:any-link {
    color: $themeHeaderTextColor;
  }
  &-country {
    &:after {
      content: "\00a0";
    }
  }
  &-city {
    &:before {
      content: "/";
    }
    &:after {
      content: "/\00a0";
    }
  }
  &-part {
    &:after {
      content: "\00a0";
    }
  }
}
