.block.is_useful {
  min-height: 500px;
  background-color: $themeLightBackground;
}
.useful-cosmonaft {
  position: absolute;
  top: 90px;
  right: 5%;
  font-size: 193px;
  color: $themeHColor;
  &:before {
    content: "{";
    position: absolute;
    left: -100px;
    top: 50px;
    transition: 0.3s;
  }
  &:after {
    content: "}";
    right: 20px;
    position: absolute;
    top: 50px;
    transition: 0.3s;
  }
  &.is_active {
    &:before {
      animation-delay: 0.5s;
      animation: leftBracket 1s;
    }
    &:after {
      animation-delay: 0.5s;
      animation: rightBracket 1s;
    }
  }
}
@keyframes leftBracket {
  0% {
    left: -100px;
  }

  50% {
    left: -115px;
  }

  100% {
    left: -100px;
  }
}

@keyframes rightBracket {
  0% {
    right: 20px;
  }

  50% {
    right: 5px;
  }

  100% {
    right: 20px;
  }
}

@media only screen and (width <= 1023px) {
  .useful-cosmonaft {
    display: none;
  }
}
